	module.exports = {"data":{"page":{"id":"/data/projects/work/self/website/content/pages/4--meta/index.md absPath of file >>> MarkdownRemark","html":"<p>This website is proudly made by the author itself, <a href=\"/about\">Alessandro Molari</a>.</p>\n<p>My mothertongue language is Italian, however I write articles in English, to make them available to everyone worldwide.\nI apologize in advance for linguistic errors.\nPlease feel free to <a href=\"/contact\">contact me</a> in private, to report mistakes and suggest improvements.\nThey’ll be very welcome!</p>\n<p>You’ll find below some generic website information, how it is structured</p>\n<h1>Tags</h1>\n<p><em>Below you can find the meaning of content tags.</em></p>\n<h2>Category tags</h2>\n<p>This tag tries to group similar content together.</p>\n<!-- TODO: Better explain this tag -->\n<h2>Status tags</h2>\n<p>Status tag shows the status of the content’s current revision.</p>\n<p>Available tags are:</p>\n<table>\n<thead>\n<tr>\n<th align=\"left\">Name</th>\n<th align=\"left\">Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"left\"><strong>Done</strong></td>\n<td align=\"left\">Revision is finished</td>\n</tr>\n<tr>\n<td align=\"left\"><strong>WIP</strong></td>\n<td align=\"left\">Revision’s content is still work in progress, and could be subject to major changes</td>\n</tr>\n<tr>\n<td align=\"left\"><strong>New</strong></td>\n<td align=\"left\">Revision is new, no content is available at the moment</td>\n</tr>\n</tbody>\n</table>\n<h2>Confidence tags</h2>\n<p>The confidence tag is a little more unusual.</p>\n<p>I use similar meaning, however the values are based exclusively on my own thoughts and no one else is involved in choosing post values.</p>\n<p>The available values I drop his other tags in favor of giving my subjective probability using the <a href=\"https://cdn.ymaws.com/www.scip.org/resource/resmgr/White_Papers/Kesselman-Verbal-Probability.pdf\">Kesselman List of Estimative Words</a> (see Page 71, Figure 5.2) with modified names:</p>\n<table>\n<thead>\n<tr>\n<th align=\"left\">Name</th>\n<th align=\"left\">Probability</th>\n<th align=\"left\">Original Name</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"left\"><strong>Certain</strong></td>\n<td align=\"left\">100%</td>\n<td align=\"left\"><em>Certain</em></td>\n</tr>\n<tr>\n<td align=\"left\"><strong>Almost Certain</strong></td>\n<td align=\"left\">86% - 99%</td>\n<td align=\"left\"><em>Almost Certain</em></td>\n</tr>\n<tr>\n<td align=\"left\"><strong>High Likely</strong></td>\n<td align=\"left\">71% - 85%</td>\n<td align=\"left\"><em>High Likely</em></td>\n</tr>\n<tr>\n<td align=\"left\"><strong>Likely</strong></td>\n<td align=\"left\">56% - 70%</td>\n<td align=\"left\"><em>Likely</em></td>\n</tr>\n<tr>\n<td align=\"left\"><strong>Possible</strong></td>\n<td align=\"left\">50%</td>\n<td align=\"left\"><em>Chances a Little Better or Less</em></td>\n</tr>\n<tr>\n<td align=\"left\"><strong>Unlikely</strong></td>\n<td align=\"left\">31% - 45%</td>\n<td align=\"left\"><em>Unlikely</em></td>\n</tr>\n<tr>\n<td align=\"left\"><strong>High Unlikely</strong></td>\n<td align=\"left\">16% - 30%</td>\n<td align=\"left\"><em>High Unlikely</em></td>\n</tr>\n<tr>\n<td align=\"left\"><strong>Remote</strong></td>\n<td align=\"left\">1% - 15%</td>\n<td align=\"left\"><em>Remote</em></td>\n</tr>\n<tr>\n<td align=\"left\"><strong>Impossible</strong></td>\n<td align=\"left\">0%</td>\n<td align=\"left\"><em>Impossible</em></td>\n</tr>\n</tbody>\n</table>\n<p>These are used to express my feeling about how well-supported the post is, or how likely it is the overall ideas are right.\nOf course, an interesting idea may be worth writing about even if very wrong, and even a long shot may be profitable to examine if the potential payoff is large enough.</p>\n<p>I stole the idea from <a href=\"https://gwern.net\">Gwern Blog</a> confidence tag.</p>\n<h2>Importance tags</h2>\n<p>An additional useful bit of metadata would be distinction between things which are trivial and those which are about more important topics which might change your life.\nI’ve ranked pages in deciles from 0-10 on how important the topic is to myself, the intended reader, or the world.</p>\n<p>I stole the idea from <a href=\"https://gwern.net\">Gwern Blog</a> importance tag.</p>","frontmatter":{"title":"Meta"}},"footnote":{"id":"/data/projects/work/self/website/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>website prodly built by <a href=\"https://alessandro.molari.me\">Alessandro Molari</a></li>\n<li>using the awesome triad\n<a href=\"https://gentoo.org\"><img src=\"/icons/gentoo.svg\" alt=\"Gentoo\"></a> &#x26;\n<a href=\"https://xmonad.org\"><img src=\"/icons/xmonad.svg\" alt=\"XMonad\"></a> &#x26;\n<a href=\"https://www.vim.org\"><img src=\"/icons/vim.svg\" alt=\"Vim\"></a></li>\n</ul>"},"site":{"siteMetadata":{"facebook":{"appId":""}}}},"pathContext":{"slug":"/meta/"}}

